<template>
  <div class="alpheios-lookup__form" v-if="uiController">
    <input class="uk-input alpheios-lookup__input" type="text" :placeholder="ln10Messages('LABEL_LOOKUP_BUTTON')" v-model="lookuptext"
      @keyup.enter="lookup"
    >
    <alph-tooltip tooltipDirection="top-right" :tooltipText="ln10Messages('LABEL_LOOKUP_BUTTON')">
      <span class="alpheios-lookup__button_with_link">
      <button class="uk-button uk-button-primary uk-button-small alpheios-lookup__button" type="button" tabindex="-1"
        @click="lookup"
      >
        {{ ln10Messages('LABEL_LOOKUP_BUTTON') }}
      </button>
      <a class="alpheios-lookup__settings-link" @click="switchLookupSettings">{{ ln10Messages('LABEL_LOOKUP_SETTINGS') }}</a>
      </span>
    </alph-tooltip>
    <div class="alpheios-lookup__settings" v-if="uiController">
      <div class="alpheios-lookup__settings-items" v-show="showLanguageSettings">
        <alph-setting :data="lookupLanguage" @change="settingChange" :classes="['alpheios-panel__options-item']"></alph-setting>

        <alph-setting :data="lexicon" @change="resourceSettingChange" :classes="['alpheios-panel__options-item']"
         v-for="lexicon in lexiconsFiltered"/></alph-setting>
      </div>
    </div>
  </div>
</template>
<script>
  import TextSelector from '../lib/selection/text-selector'
  import LexicalQueryLookup from '../lib/queries/lexical-query-lookup'
  import { LanguageModelFactory } from 'alpheios-data-models'
  import TempStorageArea from '../lib/options/temp-storage-area'

  import Tooltip from './tooltip.vue'
  import Setting from './setting.vue'

  export default {
    name: 'Lookup',
    components: {
      alphTooltip: Tooltip,
      alphSetting: Setting
    },
    data () {
      return {
        lookuptext: '',
        showLanguageSettings: false,
        options: {},
        resourceOptions: {},

        currentLanguage: null
      }
    },
    props: {
      uiController: {
        type: Object,
        required: true
      }/*,
      parentLanguage: {
        type: String,
        required: false
      }*/
    },
    created: function () {
      if (this.uiController) {
        this.options = this.uiController.options
        this.resourceOptions = this.uiController.resourceOptions

        if (this.options.items.lookupLanguage.currentValue === '') {
          this.options.items.lookupLanguage.setValue(this.options.items.lookupLanguage.defaultValue)
          this.$parent.$emit('settingchange', 'lookupLanguage', this.options.items.lookupLanguage.defaultValue)
        }

        console.log(`at creation lookup language is ${this.options.items.lookupLanguage.currentTextValue()}`)

        let vm = this
        window.onbeforeunload = function(){
          vm.$parent.$emit('settingchange', 'lookupLanguage', null)
        }
      }
    },
    computed: {
      lexiconSettingName: function() {
        let lang = this.options.items.lookupLanguage.values.filter(v => v.text === this.options.items.lookupLanguage.currentTextValue())
        let settingName
        if (lang.length>0) {
          settingName = `lexiconsShort-${lang[0].value}`
        }
        return settingName
      },
      lexiconsFiltered: function () {
        return this.resourceOptions.items.lexiconsShort.filter((item) => item.name === this.lexiconSettingName)
      },
      lookupLanguage: function () {
        return this.options.items.lookupLanguage
      }

    },
    methods: {
      'lookup': async function () {
        if (this.lookuptext.length === 0) {
          return null
        }

        let languageID = LanguageModelFactory.getLanguageIdFromCode(this.options.items.lookupLanguage.currentValue)
        let textSelector = TextSelector.createObjectFromText(this.lookuptext, languageID)

        let result = await LexicalQueryLookup
          .create(textSelector, this.uiController, this.resourceOptions)
          .getData()

        if (typeof result === 'object' && result instanceof Error) { return }
        this.clearTooltipText()
      },

      switchLookupSettings: function () {
        this.showLanguageSettings = !this.showLanguageSettings
        if (this.$parent !== undefined) {
          this.$parent.$emit('updatePopupDimensions')
        }
      },

      settingChange: function (name, value) {
        this.options.items.lookupLanguage.setTextValue(value)
        if (this.$parent !== undefined) {
          this.$parent.$emit('settingchange', 'lookupLanguage', value)
        }
      },

      resourceSettingChange: function (name, value) {
        let keyinfo = this.resourceOptions.parseKey(name)

        this.resourceOptions.items[keyinfo.setting].filter((f) => f.name === name).forEach((f) => { f.setTextValue(value) })
      },

      ln10Messages: function (value, defaultValue = 'uknown') {
        if (this.uiController && this.uiController.l10n && this.uiController.l10n.messages && this.uiController.l10n.messages[value]) {
          return this.uiController.l10n.messages[value]
        }
        return defaultValue
      },

      clearTooltipText: function () {
        this.lookuptext = ''
      }
    }
  }
</script>
<style lang="scss">
    @import "../styles/alpheios";

    .alpheios-lookup__form {
      margin: 15px 10px 5px;

      text-align: left;
    }

    .uk-input.alpheios-lookup__input {
      width: 70%;
      line-height: 28px;
      height: 30px;
      font-size: 14px;
      margin-bottom: 10px;
      vertical-align: top;

      &:focus {
        border-color: $alpheios-link-hover-color;
      }
    }

    .uk-button.alpheios-lookup__button {
      font-size: 12px;
      vertical-align: top;
      display: block;
    }

    .alpheios-lookup__settings {
      text-align: left;
    }

    a.alpheios-lookup__settings-link {
      font-size: 0.675 * $alpheios-base-font-size;
      display: block;
      padding-top: 5px;
    }

    .alpheios-lookup__button_with_link {
      width: 29%;
    }


    .alpheios-panel {
      .alpheios-lookup__form {
        width: 100%;
        margin: 5px 0;
      }
    }


    .alpheios-panel__options-item {
      max-width: none;
    }

    .alpheios-panel__options-item {
        .uk-select:not([multiple]):not([size]),
        .uk-select[multiple],
        .uk-select[size],
        .uk-textarea {
          max-width: 250px;
        }
    }

</style>
